import React, { useEffect, useState, useRef } from "react";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import * as XLSX from 'xlsx'; 
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  InputAdornment,
  Stack,
  TextField,
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Pagination,
  Paper,
  Select,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tabs,
  Typography,
  Autocomplete,
  Grid,
  Avatar,
  FormHelperText,
  Tooltip,
} from "@mui/material";
import { ToastContainer } from "react-toastify";
import Loader from "../../../common/loader/Loader";
import { payOutWalletReport , PayOutReportExcel, PayOutReportEnquiry } from "../../../api/login/Login";
import { useParams } from "react-router-dom";
import { message } from "antd";

const status = {
    1 :'Pending',
    2 : 'Success',
    3 : 'Failed',
    4 : 'Refund',
  
}

const statusColor  = {
    1 : 'btn btn-warning',
    2 : 'btn btn-success',
    3 :'btn btn-danger',
    4 :'btn btn-light'

  }

const PayOutWalletReport = () => {
    const { id} = useParams()
     const today = new Date().toISOString().split("T")[0];
      const [page, setPage] = useState(1);
      const [rowsPerPage, setRowsPerPage] = useState(10);
      const [resetKey, setResetKey] = useState(false);
      const [totalData, setTotalData] = useState(0);
      const [loading, setLoading] = useState(false);
      const [data, setData] = useState([]);

        const [filters, setFilters] = useState({
          startDate: today,
          endDate: today,
          txn_id:'',
          order_id:''
        });

          const handleChangePage = (_, value) => {
            setPage(value);
          };
          const handleChangeRowsPerPage = (event) => {
            setRowsPerPage(Number(event.target.value));
            setPage(1);
          };
        
          const handleChange = (name, value) => {
            const onlyNumbers = /^[0-9\b]+$/;
        
            // Allow only numbers for numeric fields
        
            const numberFields = ["user_mobile"];
            if (
              numberFields.includes(name) &&
              value !== "" &&
              !onlyNumbers.test(value)
            ) {
              return;
            }
            setFilters((prev) => ({
              ...prev,
              [name]: value,
            }));
          };
        
          const handleSubmit = () => {
            setResetKey((pre) => !pre);
            // you can pass `filters` to your API or logic here
          };
        
          const handleReset = () => {
            setFilters({
              startDate: "",
              endDate: "",
              txn_id:'',
              order_id:''
            });
        
            setPage(1);
            setRowsPerPage(10);
            setResetKey((pre) => !pre);
          };


                const getList = async()=> {
                             let body = {
                              page : page-1,
                              count : rowsPerPage,
                              start_date : filters.startDate,
                              end_date : filters.endDate,
                              txn_id : filters?.txn_id,
                              order_id: filters?.order_id
                             }
                           
                          try {
                             let res = await payOutWalletReport(body) 
                             if(res?.data?.statusCode === 200)
                             {
                              setData(res?.data?.data)
                              setTotalData(res?.data?.totalCount)
                             }
                             else {
                              setData([])
                              setTotalData(0)
                             }
                          
                          } catch (error) {
                              
                          }
                      }
        
          useEffect(() => {
              getList()
          }, [resetKey, page, rowsPerPage]);
        
          const totalPages = Math.ceil(totalData / rowsPerPage);


 



const downloadExcel = async () => {
  try {
    setLoading(true);
    const body = {
      ...filters,
    };

    const res = await PayOutReportExcel(body);
    const rawData = res?.data?.data || [];

    if (rawData.length > 0) {

        const exportData = rawData.map((item, index) => ({
      "Sr. No": index +1,
      "Date / Time": item?.createdAt,
      "Transaction ID": item?.txn_id,
      "Bank Account Number": item?.bank_account_number,
      "Bank IFSC": item?.bank_ifsc,
      "Bank Name": item?.bank_name,
      "Account Holder Name": item?.account_name,
      "Opening Balance": item?.opening_balance,
      "Amount": item?.amount,
      "Charge Amount": item?.charge_amount,
      "Tax On Charge" : item?.taxOnCharge,
      "Closing Balance": item?.closing_balance,
      "Payment Mode": item?.paymentMode,
      "Status": status[item?.status] || "Unknown",
      "Batch ID": item?.batchId || "",
      "UTR": item?.utr || "",
      "Message": item?.message,
      "Enquiry Number Log": item?.enqNumLog,
      "Num Log": item?.numLog,
    }));
   

      const worksheet = XLSX.utils.json_to_sheet(exportData);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Pay_Out Report");

      const fileName = `Pay_Out_Report_${new Date().toLocaleDateString()}.xlsx`;
      XLSX.writeFile(workbook, fileName);
    } else {
      message.error("No data available to export!");
    }
  } catch (error) {
    message.error("Failed to download Excel. Please try again.");
  } finally {
    setLoading(false);
  }
};


 const responseEnquiry = async (id) => {
    setLoading(true);
    try {
      let res = await PayOutReportEnquiry(id);
      if (res?.data?.error === false) {
        message.success(res.data?.message);
        
      } else {
        message.error(res.data?.message);
      }
    } catch (error) {
    } finally {
      setLoading(false);
      setResetKey((prev) => !prev);
    }
  };
    
  return (
   <>
   
     {loading && <Loader />}
   <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",

          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Typography
          variant="h6"
          sx={{
            fontWeight: "600",
            fontSize: "24px",
            color: "var(--bs-nav)",
            textAlign: "center",
          }}
        >
          Payout Report
        </Typography>
      </Paper>

      <div className="card">
        <div className="card-header"> {/* <span>Filter</span> */}</div>
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="startDate"
                  className="form-control"
                  value={filters.startDate}
                  onChange={(e) => handleChange("startDate", e.target.value)}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="endDate"
                  className="form-control"
                  value={filters.endDate}
                  onChange={(e) => handleChange("endDate", e.target.value)}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                         <label>Txn Id</label>
                         <input
                           type="text"
                           name="txn_id"
                           className="form-control"
                           value={filters.txn_id}
                           onChange={(e) => handleChange("txn_id", e.target.value)}
                          
                         />
                       </div>
                       {/* <div className="form-group col-md-4">   
                       <label>Order Id</label>
                         <input
                           type="text"
                           name="order_id"
                           className="form-control"
                           value={filters.order_id}
                           onChange={(e) => handleChange("order_id", e.target.value)}
                          
                         />
                       </div> */}
                       
              <div className="form-group col-md-12 col-lg-12">
                <div>
                  <button
                    type="button"
                    className="btn btn-primary mr-3"
                    onClick={handleSubmit}
                  >
                    Search
                  </button>
                  <button
                    type="button"
                    className="btn btn-light"
                    onClick={handleReset}
                  >
                    Reset
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>

      <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",
          marginTop: 4,
          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Box display={"flex"} justifyContent={"flex-end"} columnGap={3}>
          <Button
            variant="contained"
            color="nav"
            sx={{
              borderColor: "var(--bs-nav)", // Custom colors

              backgroundColor: "var(--bs-nav)", // Custom colors
              color: "white",
            }}
            onClick={downloadExcel}
          >
            Download Excel
          </Button>
       
        </Box>

        <Box sx={{ padding: 1 }}>
          <TableContainer component={Box} elevation={3}>
     <Table>
  <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
    <TableRow>
      <TableCell><strong>S.No.</strong></TableCell>
      <TableCell sx={{minWidth:'180px'}}><strong>Created Date</strong></TableCell>
      <TableCell><strong>Txn Id</strong></TableCell>
      <TableCell sx={{minWidth:'180px'}}><strong>Bank Account</strong></TableCell>
      <TableCell><strong>IFSC</strong></TableCell>
      <TableCell sx={{minWidth:'180px'}}><strong>Bank Name</strong></TableCell>
      <TableCell sx={{minWidth:'180px'}}><strong>Account Name</strong></TableCell>
      <TableCell ><strong>Opening Balance</strong></TableCell>
      <TableCell><strong>Amount</strong></TableCell>
      <TableCell><strong>Charge Amount</strong></TableCell>
      <TableCell><strong>Tax On Charge</strong></TableCell>
      
      <TableCell><strong>Closing Balance</strong></TableCell>
      <TableCell><strong>Payment Mode</strong></TableCell>
      <TableCell><strong>Status</strong></TableCell>
      {/* <TableCell><strong>Batch ID</strong></TableCell> */}
      <TableCell><strong>UTR</strong></TableCell>
      {/* <TableCell sx={{minWidth:'250px'}}><strong>Message</strong></TableCell> */}
      {/* <TableCell sx={{minWidth:'250px'}}><strong>Enquiry Number Log</strong></TableCell>
      <TableCell sx={{minWidth:'250px'}}><strong>Num Log</strong></TableCell> */}
      <TableCell><strong>Enquiry</strong></TableCell>
    </TableRow>
  </TableHead>

  <TableBody>
    {data?.map((item, index) => (
      <TableRow key={index} hover>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{index + 1 + (page - 1) * rowsPerPage}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.createdAt || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.txn_id || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.bank_account_number || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.bank_ifsc || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.bank_name || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.account_name || '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.opening_balance != null ? item.opening_balance : '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.amount != null ? item.amount : '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.charge_amount != null ? item.charge_amount : '-'}</TableCell>
         <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.taxOnCharge ?? '-'}</TableCell>
        
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.closing_balance != null ? item.closing_balance : '-'}</TableCell>
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.paymentMode || '-'}</TableCell>
        <TableCell>
          <button
            className={`btn ${statusColor[item?.status]}`}
            style={{ width: '130px' }}
          >
            {status[item?.status] || 'Unknown'}
          </button>
        </TableCell>
        {/* <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.batchId || '-'}</TableCell> */}
        <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.utr || '-'}</TableCell>
        {/* <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.message || '-'}</TableCell> */}
        {/* <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.enqNumLog || '-'}</TableCell> */}
        {/* <TableCell sx={{ color: "var(--bs-nav)" }}>{item?.numLog || '-'}</TableCell> */}
        <TableCell>
          <button
            className="btn btn-primary"
            onClick={() => responseEnquiry(item?._id)}
            disabled={item?.status !== 1}
          >
            Enquiry
          </button>
        </TableCell>
      </TableRow>
    ))}
  </TableBody>
</Table>



                            </TableContainer>

          {/* Pagination + Rows Per Page */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              {/* Total Entries Count */}
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalData}
              </Typography>
            </Box>

            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              // color="primary"
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": {
                    backgroundColor: "var(--bs-primary1)",
                  },
                },
              }}
            />
          </Box>
        </Box>
      </Paper>

   
   </>
  )
}

export default PayOutWalletReport